.detail {
  margin-bottom: 80px;

  &__container {
    display: flex;
  }

  &__content {
    padding: 50px 0 50px 100px;
  }

  &__article {
    display: flex;
    flex-direction: column;
    row-gap: 50px;
  }

  &__title {
    width: 100%;
    margin-bottom: 15px;
  }

  &__list {
    margin-top: 20px;
  }

  &__dimensions {
    display: flex;
    flex-wrap: wrap;
    column-gap: 50px;
  }

  &__block {
    display: flex;
    flex-direction: column;
    row-gap: 12px;
  }

  &__bot {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media (max-width: 1440px) {
  .detail {
    &__container {
      gap: 30px;
    }

    &__img {
      max-width: 485px;
      width: 100%;
    }

    &__content {
      padding: 0;
      max-width: 485px;
    }
  }
}

@media (max-width: 1440px) {
  .detail {
    &__container {
      flex-wrap: wrap;
    }

    &__img {
      max-width: 1000px;
      width: 100%;
    }

    &__content {
      padding: 0;
      max-width: 1000px;
    }
  }
}

@media (max-width: 1440px) {
  .detail {
    &__container {
      min-width: 500px;
    }

    &__picture {
      width: 100%;
    }

    &__img {
      max-width: 1000px;
      height: 380px;
      object-fit: cover;
    }

    &__content {
      max-width: 344px;
      margin: 0 auto;
    }

    &__bot {
      flex-direction: column;
      align-items: normal;
      row-gap: 15px;
    }

    &__stepper {
      width: 100%;
    }
  }
}
