.nav {
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 43px;
    row-gap: 15px;
  }

  &__item {
    padding: 2px;
  }

  &__list--column {
    align-items: normal;
    flex-direction: column;
    justify-content: normal;
    row-gap: 10px;
    margin-top: 10px;
  }

  &__link {
    color: #726e8d;
  }

  &__link--white {
    color: var(--white);
  }

  &__title {
    color: var(--white);
  }

  &__link--border {
    display: block;
    width: 22px;
    height: 22px;

    &:focus {
      outline: 1px solid #e39210;
    }
  }
}

@media (max-width: 1440px) {
  .nav {
    &__list--none {
      display: none;
    }
  }
}
