.filter {
  display: flex;
  flex-direction: column;
  column-gap: 16px;
  row-gap: 50px;

  &__fieldset {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__drop-block {
    display: none;
  }
}

@media (max-width: 500px) {
  .filter {
    display: none;

    &__drop-block {
      display: none;
    }
  }
}
