.search {
  position: absolute;
  display: none;
  top: 0;
  left: 0;
  align-items: center;
  max-width: 500px;
  width: 100%;
  height: 50px;
  background-color: var(--light-grey);
  z-index: 1;

  &__form {
    width: 100%;
    height: 50px;
  }

  &__inp {
    max-width: 304px;
    width: 100%;
    height: auto;
    background-color: #000;
  }

  &__btn {
    margin-left: -4px;
    height: auto;
    padding: 11px 30px;
  }
}

.close {
  background-color: #8c9595;
}

.open-search {
  display: flex;
}

@media (max-width: 1000px) {
  .search {
    max-width: 400px;
    right: 0;
    left: auto;
    height: 35px;

    &__form {
      width: 100%;
      height: 35px;
    }

    &__inp {
      max-width: 385px;
      width: 100%;
      height: 35px;
      padding: 5px;
    }

    &__btn {
      height: 35px;
      padding: 3px 30px;
    }
  }
}

@media (max-width: 500px) {
  .search {
    max-width: 344px;

    &__inp {
      max-width: 204px;
    }

    &__btn {
      width: 75px;
      height: 35px;
      padding: 3px 10px;
    }
  }
}
