.footer {
  background: var(--dark-primary);
  padding: 58px 0 25px 0;

  &__top {
    display: flex;
    justify-content: space-between;
    padding-bottom: 45px;
  }

  &__left {
    display: flex;
    flex-wrap: wrap;
    column-gap: 107px;
    row-gap: 15px;
  }

  &__right {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    max-width: 630px;
    width: 100%;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid var(--primary);
    padding-top: 24px;
  }

  &__span {
    color: var(--white);
  }

  &__list {
    display: flex;
    align-items: center;
    column-gap: 30px;
  }
}

@media (max-width: 1440px) {
  .footer {
    &__top {
      flex-wrap: wrap;
      justify-content: center;
    }

    &__left {
      width: 100%;
      justify-content: space-evenly;
    }
  }
}

@media (max-width: 1000px) {
  .footer {
    padding: 37px 0 25px 0;

    &__top {
      flex-wrap: wrap;
      row-gap: 38px;
      padding-bottom: 15px;
    }

    &__left {
      flex-wrap: wrap;
      column-gap: 70px;
      row-gap: 36px;
    }

    &__right {
      row-gap: 12px;
    }

    &__list {
      display: none;
    }

    &__bottom {
      padding-top: 20px;
      justify-content: center;
    }
  }
}

@media (max-width: 500px) {
  .footer {
    &__left {
      justify-content: start;
    }
  }
}
