.basket {
  background-color: var(--light-grey);
  margin-top: -55px;
  padding: 85px 0 85px;

  &__title--disp {
    display: none;
  }

  &__container {
    background-color: var(--white);
    padding: 70px 60px 55px;
  }

  &__top {
    padding-bottom: 16px;
    border-bottom: 1px solid var(--light-grey);
  }

  &__content {
    margin: 30px 0;
  }

  &__list {
    display: grid;
    row-gap: 20px;
  }

  &__bot {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid var(--light-grey);
    padding-top: 20px;
  }

  &__purchase {
    display: flex;
    max-width: 172px;
    flex-wrap: wrap;
    align-items: center;
  }

  &__span {
    margin-left: 16px;
  }

  &__span--color {
    color: var(--primary);
  }

  &__btn {
    margin-top: 16px;
  }

  &__warning {
    color: var(--primary);
  }
}

@media (max-width: 1000px) {
  .basket {
    background-color: var(--white);
    margin: 35px 0;
    padding: 0;

    &__title--disp {
      display: block;
    }
  }
}

@media (max-width: 768px) {
  .basket {
    &__container {
      padding: 0;
    }
    &__top {
      display: none;
    }

    &__sum {
      text-align: center;
    }
  }
}
