.card {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__content {
    padding: 10px 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    flex-grow: 1;
  }

  &__img {
    width: 100%;
  }

  &__title {
    margin: 0;
  }

  &__title--m {
    margin-top: 10px;
  }

  &__link {
    color: var(--dark-primary);
  }

  &__descr {
    margin-top: 10px;
    margin-bottom: 30px;
  }

  &__text {
    margin: 0;
    margin-top: 10px;
  }

  &__item--big {
    grid-column: 2 span;
  }
}

@media (max-width: 1440px) {
  .card {
    &__img {
      width: 100%;
      object-fit: cover;
      height: auto;
    }

    &__item--content {
      grid-column: 1 span;
    }
  }
}

@media (max-width: 1000px) {
  .card {
    &__item--width {
      width: 44%;
    }

    &__item--content {
      width: auto;
    }

    &__text--fs {
      margin-top: 14px;
      font-size: 14px;
    }

    &__title--fs {
      font-size: 16px;
    }

    &__img--width {
      max-width: 100%;
      max-height: max-content;
    }
  }
}

@media (max-width: 768px) {
  .card {
    &__img {
      width: 100%;
      max-width: 500px;
    }

    &__img--big {
      max-width: 1000px;
      max-height: 225px;
    }
  }
}

@media (max-width: 500px) {
  .card {
    &__item--width {
      width: 100%;
    }
  }
}
