.card-mini {
  display: flex;

  &__content {
    padding: 12px 24px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 232px;
  }

  &__title {
    margin: 0;
  }

  &__link {
    color: var(--dark-primary);
  }

  &__description {
    margin: 0;
  }
}

@media (max-width: 768px) {
  .card-mini {
    &__item {
      grid-column: 2 span;
    }
  }
}
