.popular {
  margin-bottom: 50px;

  &__list {
    margin-top: 25px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    row-gap: 10px;
  }

  &__bot {
    display: flex;
    justify-content: center;
    margin-top: 25px;
  }

  &__item--big {
    grid-column: 2 span;
  }
}

@media (max-width: 1440px) {
  .popular {
    &__list {
      flex-wrap: wrap;
    }
  }
}

@media (max-width: 1000px) {
  .popular {
    &__title {
      font-size: 20px;
    }

    &__list {
      margin-top: 33px;
      grid-template-columns: repeat(2, 1fr);
    }
  }
}
