.header {
  padding: 20px 0;
  margin-bottom: 55px;

  &__container {
    position: relative;
  }

  &__container--width {
    max-width: 1384px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding-bottom: 15px;
  }

  &__burger {
    display: none;
  }

  &__logo {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 24px;
    color: #22202e;
  }

  &__block {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 50px;
    width: 100%;
  }

  &__bottom {
    padding-top: 20px;
  }
}

@media (max-width: 1440px) {
  .header {
    padding: 20px 0;
    margin-bottom: 55px;

    &__container--width {
      max-width: 1000px;
    }
  }
}

@media (max-width: 1000px) {
  .header {
    margin: 0;
    padding: 17px 0;
    &__container--width {
      max-width: 768px;
    }

    &__bottom {
      display: none;
    }

    &__top {
      padding: 0;
      border-bottom: none;
    }

    &__mob {
      display: flex;
      column-gap: 15px;
      order: 2;
    }

    &__burger {
      display: block;
    }

    &__logo {
      order: 1;
    }

    &__block {
      display: none;
    }
  }
}

@media (max-width: 768px) {
  .header {
    &__container--width {
      max-width: 500px;
    }
  }
}

@media (max-width: 500px) {
  .header {
    &__container--width {
      max-width: 344px;
    }
  }
}
