.newsletter {
  background-image: url(.././img/static/5.jpg);
  padding: 100px 0 85px;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 445px;
  color: var(--white);

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    text-align: center;
    margin-bottom: 10px;
  }

  &__text {
    max-width: 470px;
    text-align: center;
  }

  &__list {
    display: flex;

    column-gap: 40px;
    margin-top: 30px;
    margin-bottom: 40px;
  }

  &__item {
    display: flex;
    align-items: center;
    column-gap: 10px;
  }

  &__form {
    background-color: var(--white);
  }
}

.form {
  &__inp--width {
    width: 360px;
  }
}

@media (max-width: 500px) {
  .newsletter {
    background-image: url(.././img/static/5.5.jpg);
    min-height: 375px;
    padding: 25px 0;

    &__container {
      align-items: flex-start;
    }

    &__title {
      text-align: start;
    }

    &__text {
      text-align: start;
    }

    &__list {
      margin: 20px 0;
      flex-direction: column;
    }
  }
}
