.hero {
  margin-bottom: 120px;

  &__content {
    display: flex;
    align-items: center;
    background-color: var(--dark-primary);
    min-height: 585px;
  }

  &__article {
    display: flex;
    padding: 65px 90px 60px 60px;
    flex-direction: column;
    align-items: flex-start;
    min-height: 585px;
  }

  &__title {
    max-width: 455px;
    margin-bottom: 55px;
    color: var(--white);
  }

  &__text {
    margin: 0;
    margin-top: auto;
    color: var(--white);
  }

  &__btn {
    margin-bottom: 15px;
  }

  &__img {
    max-height: 585px;
  }
}

@media (max-width: 1440px) {
  .hero {
    &__text {
      margin-top: 100px;
    }
  }
}

@media (max-width: 1000px) {
  .hero {
    background-color: var(--dark-primary);
    margin-bottom: 45px;

    &__article {
      padding: 40px 0 25px 0;
      display: flex;
      flex-wrap: wrap;
    }

    &__title {
      max-width: 310px;
      margin-bottom: 80px;

      order: 1;
    }

    &__btn {
      text-align: center;
      padding: 14px 22px;
      width: 100%;
      order: 3;
    }

    &__text {
      margin: 0;
      margin-bottom: 32px;
      order: 2;
    }

    &__img {
      display: none;
    }
  }
}
