.studio {
  &__container {
    max-width: 1440px;
  }

  &__content {
    display: flex;
    max-height: 600px;
  }

  &__left {
    display: flex;
    flex-wrap: wrap;
    padding: 70px 100px 55px 85px;
    flex-direction: column;
    align-items: flex-start;
  }

  &__title {
    margin-bottom: 25px;
  }

  &__text {
    margin-bottom: 20px;
  }

  &__btn {
    margin-top: auto;
  }
}

@media (max-width: 1440px) {
  .studio {
    &__img {
      max-width: 500px;
    }
  }
}

@media (max-width: 1000px) {
  .studio {
    &__content {
      flex-wrap: wrap;
      max-height: 100%;
    }

    &__left {
      max-width: 768px;
      margin: 0 auto;
      padding: 33px 0 40px;
    }

    &__title {
      font-size: 20px;
      margin-bottom: 13px;
    }

    &__text {
      font-size: 14px;
    }

    &__btn {
      margin-top: 48px;
      width: 100%;
    }

    &__right {
      width: 100%;
      height: 360px;
    }

    &__img {
      width: 100%;
      max-width: 1000px;
      min-height: 360px;
    }
  }
}

@media (max-width: 768px) {
  .studio {
    &__left {
      max-width: 500px;
    }
  }
}

@media (max-width: 500px) {
  .studio {
    &__left {
      max-width: 344px;
    }
  }
}
