.all {
  background-image: url(.././img/static/2.jpg);
  margin-top: -55px;
  margin-bottom: 35px;
  padding: 125px 0 34px;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 210px;

  &__title {
    color: var(--white);
  }
}

@media (max-width: 1000px) {
  .all {
    margin-top: 0;
    margin-bottom: 20px;

    &__title {
      text-align: center;
    }
  }
}

@media (max-width: 500px) {
  .all {
    padding: 60px 0 35px;
    background-image: url(.././img/static/2.2.jpg);
    min-height: 150px;
  }
}
