.new {
  margin-bottom: 120px;

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    row-gap: 10px;
  }
  &__title {
    letter-spacing: 1.5px;
  }

  &__list {
    margin-top: 30px;
  }

  &__bot {
    display: flex;
    justify-content: center;
    margin-top: 25px;
  }
}

