//     }
//   }
// }

@media (max-width: 1000px) {
  .new {
    margin-bottom: 88px;

    &__title {
      font-size: 20px;
    }

    &__list {
      margin-top: 20px;
    }

    &__bot {
      margin-top: 20px;
    }

    &__btn {
      text-align: center;
      width: 100%;
    }
  }
}

@media (max-width: 768px) {
  .new {
    &__list {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}
