.link {
  outline: none;
  border: 1px solid transparent;
  transition: border 0.3s ease-in-out, color 0.3s ease-in-out;

  &:focus {
    border: 1px solid var(--primary);
  }

  &:hover {
    color: var(--border-dark);
  }

  &:active {
    color: var(--dark-primary);
  }
}

.form {
  &__btn {
    margin-left: -4px;
  }

  &__inp--widht {
    max-width: 354px;
    width: 100%;
  }
}

.form--widht {
  max-width: 475px;
  width: 100%;
}

.svg-social {
  fill: var(--white);
  transition: fill 0.3s ease-in-out;

  &:hover {
    fill: var(--border-dark);
  }

  &:active {
    fill: #cdc69b;
  }
}

.d-grid {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  column-gap: 200px;
}

@media (max-width: 1440px) {
  .container {
    max-width: 1000px;
  }

  .d-grid {
    column-gap: 100px;
  }
}

@media (max-width: 1000px) {
  .container {
    max-width: 768px;
  }
  .d-grid {
    column-gap: 25px;
    grid-template-columns: 2fr 125px 60px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 500px;
  }

  .d-grid {
    column-gap: 25px;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 10px;
  }
}

@media (max-width: 500px) {
  .container {
    max-width: 344px;
  }
}
