.burger-menu {
  display: none;
}

@media (max-width: 1000px) {
  .burger-menu {
    display: none;
    align-items: center;
    justify-content: space-around;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 1000px;
    background: var(--white);
    height: 100vh;
    padding: 30px;

    &__list {
      display: flex;
      flex-direction: column;
      gap: 30px;
    }

    &__btn {
      position: absolute;
      top: 30px;
      right: 30px;
    }
  }

  .open {
    display: flex;
  }
}

@media (max-width: 768px) {
  .burger-menu {
    align-items: normal;
    padding: 60px 30px;

    &__right {
      display: none;
    }

    &__list {
      flex-direction: row;
      flex-wrap: wrap;
      max-width: 290px;
    }

    &__item {
      max-width: 120px;
      width: 100%;
    }
  }
}
