.products {
  margin-bottom: 35px;

  &__container {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: 20px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
  }

  &__filter {
    grid-row-start: 1;
    grid-row-end: 3;
  }

  &__btn {
    max-width: 141px;
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .products {
    &__list {
      grid-template-columns: repeat(2, 1fr);
    }

    &__container {
      display: grid;
      grid-template-columns: 175px 1fr;
    }
  }
}

@media (max-width: 500px) {
  .products {
    &__container {
      display: grid;
      grid-template-columns: 1fr;
    }
  }
}
