.brand {
  margin-bottom: 152px;

  &__title {
    margin: 0;
    color: var(--dark-primary);
    text-align: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 60px;
    margin-top: 52px;
  }
}

@media (max-width: 1000px) {
  .brand {
    margin-bottom: 80px;

    &__title {
      max-width: 275px;
      font-size: 20px;
      text-align: start;
    }

    &__list {
      margin-top: 38px;
      grid-template-columns: repeat(1, 1fr);
      row-gap: 25px;
    }
  }
}
